<template>
	<view class="con">
		<template v-if="List.length == 0">
			<view class="top"></view>
			<view class="flex msg">
				<image src="../../static/logo.png" mode=""></image>
				<view class="right">
					<view class="t">系统通知</view>
					<view class="b">暂无消息通知</view>
				</view>
			</view>
		</template>
		<template v-if="List.length>=1">
			<view :style="{ height: scrollH + 'px' }" class='con1' >
				<scroll-view scroll-x class="scrollBox">
					<view class="items" v-for="(item, index) in List" :key="index">
						<!-- <view class="time">09-23</view> -->
						<view class="msgItem">
							<view class="headMsg">{{item.content}}</view>
							<view class="kpTime flex">
								<image src="../../static/icon/shijian.png" mode=""></image>
								<view>看铺时间{{item.createTime}}</view>
							</view>
							<view class="detailsMsg flex">
								<image :src="item.resUrl" mode=""></image>
								<view class="right">
									<view class="t">{{item.storeTitle}}</view>
									<view class="m">{{item.streetTitle}}｜{{item.showAddress}}</view>
									<view class="b">{{item.rent}}<text>{{item.floorSpace}}㎡</text></view>
								</view>
							</view>
						</view>
					</view>
					
					
				</scroll-view>
				
			</view>
		</template>
		
	</view>
	
</template>

<script>
	export default {
		onLoad(){
			this.userId = uni.getStorageSync('userInfo').userId
			this.token = uni.getStorageSync('userInfo').token
			this.getList()
		},
		onReady() {
				uni.getSystemInfo({
					success: (res)=> {
						this.scrollH = res.windowHeight 
					}
				});
			},
		data() {
			return {
				messageLenth: false,
				scrollH: 0,
				userId:'',
				List:[]
			};
		},
		methods:{
			getList(){
				uni.showLoading({
					title: '加载中'
				});
				let params = {
					userId: this.userId
				}
				this.$http.post(this.$api.findMessage,{
					data: params,
					header: {
						'content-type': 'application/json;charset=UTF-8',
						token: this.token
					}
				}).then(res=>{
					if(res.data.code == 0){
						uni.hideLoading();
						this.List = res.data.data
					}else{
						this.toast(res.data.msg)
					}
				})
			}
		}
	}
</script>

<style lang="less" scoped>
	.con {
		width: 750rpx;
		.top {
			box-shadow: 8rpx 1rpx 8rpx 1rpx rgba(4, 0, 0, 0.06);
		}
		.msg{
			margin-top:38rpx;
			margin-left: 36rpx;
			align-items: center;
			image{
				width: 102rpx;
				height: 102rpx;
			}
			.right{
				margin-left: 30rpx;
				.t{
					// margin-top: 10rpx;
					font-size: 34rpx;
					font-family: PingFang SC;
					font-weight: 600;
					color: #343434;
				}
				.b{
					margin-top: 12rpx;
					font-size: 26rpx;
					font-family: PingFang SC;
					font-weight: 400;
					color: #909090;
				}
			}
		}
	}
	.con1{
		width: 750rpx;
		background: #F9F9F9;
		padding: 30rpx 0 ;
		box-sizing: border-box;
		.items{
			width: 100%;
			padding-bottom: 34rpx;
			box-sizing: border-box;
			background: #FFFFFF;
			box-shadow: 0rpx 0rpx 8rpx 1rpx rgba(4, 0, 0, 0.06);
			border-radius: 12rpx;
			margin: 0rpx 36rpx 30rpx 36rpx;
			widtH: 670rpx;
			.time{
				height: 102rpx;
				width: 100%;
				line-height: 102rpx;
				text-align: center;
				font-size: 24rpx;
				font-family: PingFang SC;
				font-weight: 400;
				color: #909090;
			}
			.msgItem{
				.headMsg{
					padding: 33rpx 0 33rpx 30rpx;
					box-sizing: border-box;
					font-size: 32rpx;
					font-family: PingFang SC;
					font-weight: 600;
					color: #343434;
					border-bottom: 2rpx solid #EEEEEE;
				}
				.kpTime{
					margin-top: 23rpx;
