					}
					view{
						margin-left: 9rpx;
						font-size: 30rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: #000000;
					}
				}
				.detailsMsg{
					margin-top:30rpx;
					align-items: center;
					 image{
						 width: 202rpx;
						 height: 150rpx;
						 border-radius: 12rpx;
						 margin-left: 30rpx;
					 }
					 .right{
						 flex: 1;
						 margin-left: 34rpx;
						 .t{
							font-size: 32rpx;
