							font-weight: 600;
							color: #343434; 
						 }
						 .m{
							 margin-top: 23rpx;
							 font-size: 24rpx;
							 font-family: PingFang SC;
							 font-weight: 400;
							 color: #909090;
						 }
						 .b{
							 margin-top: 23rpx;
							font-size: 30rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #FF6633; 
							text{
								margin-left: 12rpx;
								font-size: 24rpx;
								font-family: PingFang SC;
								font-weight: 400;
